declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { errorMessage } from 'src/app/messages';
import { PaginationService } from 'src/app/service/pagination.service';
import { DataService } from 'src/app/service/data.service';
import { OrderService } from 'src/app/service/order.service';
import { TextField, ActionField, TextViewUrlField, DateField, StatusField, MultiStatusField } from 'src/app/common-module/table-list/list-field-type';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html'
})
export class OrderListComponent implements OnInit {
  PageTitle = "Manage Order";
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  statusListArray: Array<any> = [];
  stagesListArray: Array<any> = [];


  headerData: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;

  permissionObject: any = null;
  status_id: any = 1;
  satage_id: any;
  constructor(
    private paginationService: PaginationService,
    private orderService: OrderService,
    private dataService: DataService
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['activity'];
    // });
    this.getMasterData();
    this.setHeaderData();
    this.loadform = false;
    this.getObjects();
  }
  getMasterData() {
    this.orderService.getOrderfilterData().subscribe((responce) => {
      if (responce.success) {
        this.statusListArray = responce.data.status;
        this.stagesListArray = responce.data.status_type;

      }
    });
  }

  setHeaderData() {
    this.headerData.push(
      new TextViewUrlField({
        label: "Order #",
        key: "po_no",
        viewUrl: {
          path: '/inbound/order/view/',
          id: 'po_id'
        }
      }),
      new DateField({
        label: "PO Date",
        key: "po_date",
      }),
      new TextField({
        label: "Case #",
        key: '',
      }),
      new TextField({
        label: "Supplier",
        key: "supplier.label",
      }),
      new TextField({
        label: "Client",
        key: "client.label",
      }),

      new MultiStatusField({
        label: "Status",
        key: "status",
      }),
      
    );
    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/inbound/order/edit/',
        id: 'po_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/inbound/order/view/',
        id: 'po_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'po/',
        id: 'po_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }


  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.status_id) {
      params.po_status = this.status_id;
    }

    if (this.satage_id && this.status_id && this.status_id == 1) {
      params.status_id = this.satage_id;
    }
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.orderService.getOrderList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success && response.data) {
        this.objectArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    // this.currentPage = 1;
    this.getObjects();
  }

  onReloadEvent() {
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }

  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.orderService.deleteOrder(object.po_id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }
}






